import { BrowserRouter, Route, Routes } from 'react-router-dom';
import { ThemeProvider } from 'styled-components';
import Nav from './components/ui/Nav/Nav';
import { Example } from './components/views/Example/Example';
import MainView from './components/views/MainView/MainView';
import ModelsPreview from './components/views/ModelsPreview/ModelsPreview';
import RotatingBoxes from './components/views/RotatingBoxes/RotatingBoxes';
import { GlobalStyles } from './styles/GlobalStyles';
import { Reset } from './styles/Reset';
import { theme } from './styles/theme';

function App() {
	return (
		<ThemeProvider theme={theme}>
			<Reset />
			<GlobalStyles />
			<BrowserRouter>
				<Nav />
				<Routes>
					<Route path="Threejs_React_Typescript" element={<MainView />} />
					<Route path="Threejs_React_Typescript/box" element={<Example />} />
					<Route
						path="Threejs_React_Typescript/modelspreview"
						element={<ModelsPreview />}
					/>
					<Route
						path="Threejs_React_Typescript/rotatingboxes"
						element={<RotatingBoxes />}
					/>
				</Routes>
			</BrowserRouter>
		</ThemeProvider>
	);
}

export default App;
